/* hamburger
------------------------------------------------------ */
@use "../base/setting" as *;
@use "../global/" as *;

.hamburger {
  @include z-index(hamburger);

  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  cursor: pointer;

  @include mq(md) {
    display: none;
  }
}

.hamburger__line {
  position: relative;
  width: 30px;
  height: 2px;
  background: $c_black;
}

// 上下の線、幅
.hamburger__line::before,
.hamburger__line::after {
  position: absolute;
  display: block;
  width: 30px;
  height: 2px;
  content: "";
  background: $c_black;
  transition: transform 0.3s;
}

// 線の高さ
.hamburger__line::before {
  top: -8px;
}

.hamburger__line::after {
  bottom: -8px;
}

/* 閉じる */
.hamburger.close .hamburger__line {
  background: transparent;
}

.hamburger.close .hamburger__line::before {
  top: 0;
  transform: rotate(45deg);
}

.hamburger.close .hamburger__line::after {
  bottom: 0;
  transform: rotate(-45deg);
}
