/* header
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global/mixin/breakpoint"as *;

.header {
  padding: 24px 0px;
}

.header__inner {
  max-width: $width;
  padding-left: $padding-side-sp;
  padding-right: $padding-side-sp;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include mq(md) {
    margin: 0 auto;
    padding-left: $padding-side;
    padding-right: $padding-side;
  }
}

.header-nav {
  width: 100%;
  height: 100vh;
  visibility: hidden; //display:noneはtransitionが効かない
  position: fixed;
  top: 0;
  left: 0;
  background-color: $c_black;


  @include mq(md) {
    position: static;
    width: auto;
    height: auto;
    visibility: visible;
    background-color: transparent;

  }
}

.header-nav.fade {
  visibility: visible;
  opacity: 1;
}

.header-nav__list {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  list-style: none;
  text-align: center;

  @include mq(md) {
    position: static;
    display: flex;
    transform: none;
  }
}

.header-nav__link {
  display: block;
  margin-bottom: 50px;

  @include mq(md) {
    margin-bottom: 0px;
    margin-right: 55px;
  }
}